.lsv-nav{
  background-color: @lsv_menu_color;
  height: @lsv_menu_height;
}

.lsv-nav--valign{
  position: absolute;
  top: 0;
  bottom: 0;
  left: @lsv_logo_displace;
  width: 0;
  height: 67%;
  margin: auto;
}

.lsv-nav__logo{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 46%;
  width: 0;
  height: 67%;
  margin: auto;

  @media screen and (max-width: 800px){
    left: @lsv_menu_height*2 + 30px;
  }
  @media screen and (max-width: 420px){
    left: @lsv_menu_height + 20px;
  }
}

.lsv-nav__menu-btn{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: @lsv_menu_height*2;
  border: 0;
  padding: 0;
  background-color: @lsv_green;
  @media screen and (max-width: 420px){
    width: @lsv_menu_height;
  }
  &:focus{
    outline: none;
  }

  &:hover{
    background-color: @lsv_green_active;
  }
  &:hover .line{
    // transform: translate3d(0,-7px,0);
    top: -7px;
  }
  &:hover .line01{
    opacity: 0;
    transition-delay: 0ms;
  }
  &:hover .line02{
    transition-delay: @lsv_menu_btn_delay;
  }
  &:hover .line03{
    transition-delay: @lsv_menu_btn_delay*2;
  }
  &:hover .line04{
    opacity: 1;
    transition-delay: @lsv_menu_btn_delay*3;
  }
  i{
    color: white;
    font-size: 24px;
  }
}

.lsv-nav__menu-btn--active{
  background-color: @lsv_green_active;
  transition-delay: 0ms;
  &:hover .line {
    top: -7px;
  }
  & .line{
    top: -7px;
  }
  & .line01{
    opacity: 0;
  }
  & .line02{
    opacity: 1;
    transform: translate3d(0,7px,0) rotate(-45deg);
    transform-origin: center center 0px;
  }
  & .line03{
    opacity: 0;
  }
  & .line04{
    opacity: 1;
    transform: translate3d(0,-7px,0) rotate(45deg);
    transform-origin: center center 0px;
  }
}

.bars-container {
    position: relative;
    backface-visibility: hidden;
}
.lsv-menu-btn__text{
  color: white;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  position: absolute;
  right: 0;
  margin: 20px 16px;
