.lsv-radio {
  vertical-align: top;
  width: 26px;
  height: 26px;
  margin: 0 3px 0 0;
}
.lsv-radio + label {
  cursor: pointer;
  text-transform: none;
  margin-bottom: 15px;
  display: block;
  white-space: nowrap;
}
.lsv-radio:not(checked) {
  position: absolute;
  opacity: 0;
}
.lsv-radio:not(checked) + label {
  position: relative;
  padding: 0 0 0 35px;
}
.lsv-radio:not(checked) + label:before {
  content: '';
  position: absolute;
  top: -2px;
  left: 0;
  width: 26px;
  height: 26px;
  // border: 1px solid black;
  border-radius: 50%;
  background: #FFF;
}
.lsv-radio:not(checked) + label:after {
  content: '';
  position: absolute;
  top: 6px;
  left: 8px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: @lsv_green;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.5);
  opacity: 0;
  transition: all .2s;
}
.lsv-radio:checked + label:after {
  opacity: 1;
}
.lsv-radio:focus + label:before {
  box-shadow: 0 0 2px 2px @lsv_green_active;
}

